'use strict';

var React = require('react');
var Reflux = require('reflux');
var InfoWindowStore=require('../../../../stores/infoWindowStore.js');
var MyChart=require('./_charts.js');


var MyActivities = React.createClass({displayName: "MyActivities",
  render: function() {
    var items = this.props.data;
    return (
        React.createElement("ul", null, 
        
          items.map(function(node, index) {
            return React.createElement("li", null, React.createElement("a", {href: node.link, target: "_blank"}, node.name))          
          })
        
        )
    );
  }
});

module.exports  = React.createClass({displayName: "exports",
  mixins: [Reflux.connect(InfoWindowStore)],


  componentDidUpdate: function(props,newState) {  
    this.props.onChange();
  },


  handleClick:function(tabId){
    console.log('popup>click');
    this.setState({'tabId':tabId});
    this.forceUpdate();
    
  },

  render: function() {
    console.log('popup>render id:' + this.props.id +" tab "+this.state.tabId);
    var self = this;
    var chartData = [];
    var titleArray = [];
    if(self.state.infoWindow){
      self.state.infoWindow.map(function(node, index) {
        titleArray.push(node.title)
        node.value.map(function(innerNode, index) {
          if(innerNode.id==self.props.id){
            chartData.push(innerNode.value);
          } else {
            chartData.push();
          }
        });
      });
    } 
    debugger;
    var content=(  React.createElement("div", {className: "popup-content"}, React.createElement("h4", null, titleArray[0]), React.createElement(MyChart, {data: chartData[0]})))
 
    if (this.state.tabId==0){
      content=(  React.createElement("div", {className: "popup-content"}, React.createElement("h4", null, titleArray[0]), React.createElement(MyChart, {data: chartData[0]})))
    }
    if (this.state.tabId==1){
      content=(  React.createElement("div", {className: "popup-content"}, React.createElement("h4", null, titleArray[1]), React.createElement(MyChart, {data: chartData[1]})))
    }
    if (this.state.tabId==2){
      content=(  React.createElement("div", {className: "popup-content"}, React.createElement("h4", null, titleArray[2]), React.createElement(MyChart, {data: chartData[2]})))
    }
    if (this.state.tabId==3){
      content=(  React.createElement("div", {className: "popup-content"}, React.createElement("h4", null, titleArray[3]), React.createElement(MyActivities, {data: chartData[3]})))
    }
    return (
      React.createElement("div", {className: "leaflet-popup-content-wrapper"}, 
        React.createElement("div", {className: "leaflet-popup-content"}, 
          React.createElement("div", {className: "panel panel-default"}, 
            React.createElement("div", {className: "panel-heading popup-header"}, 
              React.createElement("h3", {className: "panel-title"}, this.props.name), 
              React.createElement("span", {className: "title-label"}, " - Total Activities")
            ), 
            React.createElement("div", {className: "popup-nav-wrapper"}, 
              React.createElement("nav", {className: "tabs", role: "tablist"}, 
                React.createElement("ul", {className: "tabs nav nav-tabs", role: "tablist"}, 
                React.createElement("li", {className: "active", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 0)}, 
                    React.createElement("span", {className: "popup-icon chart"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 1)}, 
                    React.createElement("span", {className: "popup-icon funding-dev-obj"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 2)}, 
                    React.createElement("span", {className: "popup-icon subactivities"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 3)}, 
                    React.createElement("span", {className: "popup-icon export"})
                  )
                )
                )
              )
            ), 
            React.createElement("div", {className: "panel-body"}, content)
          )
        )
      )
    );
}

});
